import React, {Component} from 'react';
import DarkSkyApi from 'dark-sky-api';
import styled from 'styled-components';

import './App.css';
import Weather from './components/Weather';

DarkSkyApi.apiKey = 'ecce78e27f7acdbd7faa415857ef2ac6';

export default class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loaded: false,
            degreeType: 'celcius',
            weatherLocation: {},
            degrees: {
                farenheit: '',
                celcius: ''
            },
            icon: '',
        }

        this.toggleDegrees = this.toggleDegrees.bind(this);
    }

    componentDidMount() {
        this.getLocation();
    }

    render() {
        return (
            <div className="App">
                <h1 className="App__title">FCC Weather App By Ramon</h1>
                <Weather
                    toggleDegrees={this.toggleDegrees}
                    degreeType={this.state.degreeType}
                    loaded={this.state.loaded}
                    degrees={this.state.degrees}
                    icon={this.state.icon}/>
            </div>
        );
    }

    getLocation() {
        let returnLocation = (pos) => {
            this.setState({
                weatherLocation: {
                    longitude: pos.coords.longitude,
                    latitude: pos.coords.latitude
                }
            }, this.getCurrentWeather)
        };

        navigator.geolocation.getCurrentPosition(returnLocation);

    }

    getCurrentWeather() {
        DarkSkyApi.loadCurrent(this.state.weatherLocation)
            .then(res => {
                console.log(res);
                this.setState({
                    loaded: true,
                    degrees: {
                        farenheit: Math.round(res.temperature),
                        celcius: this.toCelcius(res.temperature)
                    },
                    icon: res.icon
                })
            });
    }

    toCelcius(far) {
        return Math.round((far - 32) / 1.8)
    }

    toggleDegrees(){
        let type = this.state.degreeType === 'celcius'? 'farenheit' : 'celcius';
        this.setState({
            degreeType: type
        });
    }
}




